.mat-typography {

	h1,
	h2,
	h3,
	h4 {
		font-family: 'Museo';
	}

	h1,
	h2 {
		line-height  : 1.15;
		margin-bottom: 35px;
		font-size    : $title-font-size-phone;
		font-weight  : $title-font-weight-phone;

		@include media('>laptop') {
			font-size  : $title-font-size;
			font-weight: $title-font-weight;
		}
	}

	p {
		margin-bottom: 25px;
	}
}

a.link {
	color      : $color-semi-dark;
	font-weight: $font-weight-bold;
	display    : inline-block;

	&:hover {
		color: $color-primary;
	}

	&.violet {
		&,
		&:hover {
			color: $color-violet;
		}
	}

	&.cyan {
		&,
		&:hover {
			color: $color-cyan;
		}
	}

	&.violet,
	&.cyan {
		&:after {
			content: ' ➜';
			position: static;
			opacity: 0;
			transition: opacity $animation-duration $animation-function;
			font-weight: $font-weight-slim;
		}

		&:hover:after {
			opacity: 1;
		}
	}
}

a.active {
	color: $color-primary;
}

.text-violet {
	color: $color-violet;
}
