mat-card.mat-card {
	border-radius: $border-card-radius;
	padding: 2rem 2rem 4rem;
	display: flex;
	flex-flow: column;
	position: relative;
	max-width: $card-max-width;
	width: 100%;

	@include media('>=tablet') {
		padding: 6rem 7rem;
	}

	.avatar {
		position: absolute;
		left: 0;
		transform: translate(-30%, -5%);
		border-radius: 100px;
		overflow: hidden;
		width: 88px;

		@include media('<tablet') {
			display: none;
		}

		@include media('>=widescreen') {
			transform: translate(-50%, -5%);
		}
	}

	mat-card-title {
		margin-bottom: 2rem;
		font-weight: $font-weight-normal;

		h2 {
			margin-bottom: 0.5rem;

		}

		time {
			font-weight: $font-weight-slim;
		}
	}

	mat-card-content {
		flex: 1;
		font-size: $font-size-sm;

		> mat-progress-bar {
			position: absolute;
			top: 0;
		}
	}

	mat-card-footer {
		margin: 0 (-$mat-card-padding-x) (-$mat-card-padding-y) (-$mat-card-padding-x);
	}

	&:not([class*='mat-elevation-z']) {
		@include mat-elevation(8);
	}
}
