body .mat-button {
	font-size: $button-font-size;
	font-weight: $button-font-weight;
	line-height: $button-line-height;
}

.bp-button.mat-button {
	padding     : 0.175em 1.75em;
	font-weight : $font-weight-bold;
	border-width: 0;

	&.primary {
		background-color: $color-primary;
		color: white;
	}

	&.cyan {
		background-color: $color-cyan;

		&:hover {
			color: $color-primary;
		}
	}

	&.violet {
		background-color: $color-violet;
		color           : white;

		&:hover {
			color: white;
		}
	}

	&.md {
		height : 50px;
	}

	&.lg {
		min-width    : 270px;
		border-radius: 64px;
		height       : 60px;

		@include media('>laptop') {
			height : 70px;
		}
	}
}

a.mat-button,
a.link {
	&.text-color-peach {
		color: $color-peach;
	}
}
